import React, {SyntheticEvent, useState} from 'react';
import {DataGrid, GridCellParams, GridColDef, GridRowId} from "@material-ui/data-grid";
import './styles/phone.module.scss'
import {Button, IconButton} from "@material-ui/core";
import EditIcon from "@material-ui/icons/Edit";
import {Delete} from "@material-ui/icons";
import {ContactInterface, PhoneInterface} from "../../../contactList/types/contact.interface";
import {ModalForEditForm} from "../../../../shared/components/ModalForEditForm";
import {EditPhoneForm} from "./EditPhoneForm";
import {AddPhoneForm} from "./AddPhoneForm";
import {useActions} from "../../../../store/hooks/useActions";
import {useStyles} from "../styles/formStyles";

interface PhoneFormProps {
    contact: ContactInterface
    setNewPhones?: Function
    newPhones?: Array<PhoneInterface>
}

const PhoneForm = (props: PhoneFormProps) => {

    const classes = useStyles()

    const columns: GridColDef[] = [
        {
            field: 'phone', headerName: ' ', width: 200, filterable: false, sortable: false,
            renderCell: (params: GridCellParams) => {
                return <span>{`${params.row.countryCode} ${params.row.operatorID} ${params.row.phoneNumber}`}</span>
            }
        },
        {field: 'countryCode', headerName: ' ', width: 200, filterable: false, sortable: false, hide: true},
        {field: 'operatorID', headerName: ' ', width: 200, filterable: false, sortable: false, hide: true},
        {
            field: 'phoneNumber',
            headerName: ' ',
            width: 200,
            filterable: false,
            sortable: false,
            hide: true
        },
        {field: 'phoneType', headerName: '', width: 160, filterable: false, sortable: false},
        {field: 'comment', headerName: '', width: 160, filterable: false, sortable: false, flex: 1},
        {
            field: '', headerName: '', width: 155, filterable: false, sortable: false, editable: true,headerAlign: 'center',
            renderCell: (el) => {
                return <IconButton
                    id={String(el.id)}
                    aria-label="edit"
                    onClick={changePhoneHandler}
                >
                    <EditIcon/>
                </IconButton>
            }
        },
        {
            field: '', headerName: '', width: 110, filterable: false, sortable: false,headerAlign: 'center',
            renderCell: (el) =>
                <IconButton
                    aria-label="del"
                    id={String(el.id)}
                    onClick={deleteCurrentPhone}
                >
                    <Delete/>
                </IconButton>
        },
    ]

    const {contact, setNewPhones,newPhones} = props
    let phones = contact.phones

    const {deletePhone} = useActions()
    const [open, setOpen] = useState(false);
    // const [phone, setPhone] = useState({} as PhoneInterface); //edit phone
    const [selectionModel, setSelectionModel] = useState<GridRowId[]>([]);
    const [title, setTitle] = useState<string>('');
    const [body, setBody] = useState<JSX.Element>(<div/>);
    const [buttons] = useState<JSX.Element>(<div/>);
    const handleCloseModal = () => {
        setOpen(false);
    };


// EDIT PHONE HANDLER
    const changePhoneHandler = (event: SyntheticEvent) => {
        const targetID = event.currentTarget.id
        setTitle('  ');
        setOpen(true);
        // setPhone(currentPhone)
        if(contact.id) {
            const currentPhone = contact.phones.find(target => target.id === targetID)!;
            setBody(<EditPhoneForm phone={currentPhone} setOpen={setOpen} contact={contact}/>)
        } else {
            const currentPhone = newPhones!.find(target => target.id === targetID)!;
            setBody(<EditPhoneForm phone={currentPhone} setOpen={setOpen} contact={contact} newPhones={newPhones} setNewPhones={setNewPhones}/>)

        }
    }

  // ADD PHONE HANDLER
    const addPhoneChangeHandler = (event: SyntheticEvent) => {
        setTitle('  ');
        setBody(<AddPhoneForm setOpen={setOpen} contact={contact} setNewPhones={setNewPhones} newPhones={newPhones}/>)
        setOpen(true);
    }

    const checkedCurrenPhone = (params: GridRowId[]) => {
        setSelectionModel(params)
    }

    const deleteCurrentPhone = (event: SyntheticEvent) => {
        const phoneId = event.currentTarget.id
        const contactId = contact.id
        if(!contact.id) {
            setNewPhones!(newPhones?.filter(item=> item.id !== phoneId))
        } else deletePhone(contactId, phoneId)
    }


    return (
        <div className={classes.phoneFormWrapper}>
            <h2 className={classes.formTitle}> </h2>
            <Button
                className={classes.button}
                variant="outlined"
                color="primary"
                onClick={addPhoneChangeHandler}
            >
                  
            </Button>
            <DataGrid
                className={classes.phoneTable}
                rows={phones! || newPhones}
                columns={columns}
                autoHeight
                disableSelectionOnClick
                hideFooter
                onSelectionModelChange={checkedCurrenPhone}
                selectionModel={selectionModel}
            />

            <ModalForEditForm
                open={open}
                onClose={handleCloseModal}
                title={title}
                body={body}
                buttons={buttons}
            />
        </div>
    );
};

export default PhoneForm;