import React, {ChangeEvent} from 'react';
import {Button, FormControl, FormGroup, Grid, TextField} from "@material-ui/core";
import {useStylesAddPhone} from "../phone/styles/styles";
import {ContactInterface, PhoneInterface} from "../../../contactList/types/contact.interface";
import {useActions} from "../../../../store/hooks/useActions";

interface EditPhoneFormInterface {
    setOpen: Function
    phone: PhoneInterface
    contact: ContactInterface
    newPhones?: Array<PhoneInterface>
    setNewPhones?: Function
}

export const EditPhoneForm = (props: EditPhoneFormInterface) => {

    const classes = useStylesAddPhone();
    const {editPhone} = useActions()
    let {phone, setOpen, contact, newPhones, setNewPhones} = props

    const changePhoneInfoHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const {name, value} = event.target

        if (name === 'countryCode') {
            phone.countryCode = value
        }
        if (name === 'operatorID') {
            phone.operatorID = value
        }
        if (name === 'phoneNumber') {
            phone.phoneNumber = value
        }
        if (name === 'phoneType') {
            phone.phoneType = value
        }
        if (name === 'comment') {
            phone.comment = value
        }
    }

    const onSubmit = () => {
        if(contact.id) {
            editPhone(phone, contact.id)
            setOpen(false)
        } else {
            const updatePhones = newPhones!.map((item) => {
                if (item.id === phone.id) {
                    return phone
                }
                return item

            })
            setOpen(false)
            return setNewPhones!(updatePhones)
        }
    }

    return (
        <div>
            <Grid container justifyContent="center">
                <Grid item xs={10}>
                    <form>
                        <FormControl>
                            <FormGroup>
                                <div className={classes.wrapperInput}>
                                    <TextField className={classes.input}
                                               label=" "
                                               name={"countryCode"}
                                               type="search"
                                               onChange={changePhoneInfoHandler}
                                               defaultValue={phone.countryCode ? phone.countryCode : ''}
                                    />
                                    <TextField className={classes.input}
                                               label=" "
                                               name={"operatorID"}
                                               type="search"
                                               onChange={changePhoneInfoHandler}
                                               defaultValue={phone.operatorID ? phone.operatorID : ''}
                                    />
                                    <TextField className={classes.input}
                                               label=" "
                                               name={"phoneNumber"}
                                               type="search"
                                               onChange={changePhoneInfoHandler}
                                               defaultValue={phone.phoneNumber ? phone.phoneNumber : ''}
                                    />
                                    <TextField className={classes.input}
                                               label=""
                                               name={"phoneType"}
                                               type="search"
                                               onChange={changePhoneInfoHandler}
                                               defaultValue={phone.phoneType ? phone.phoneType : ''}
                                    />
                                    <TextField className={classes.input}
                                               label=""
                                               name={"comment"}
                                               type="search"
                                               onChange={changePhoneInfoHandler}
                                               defaultValue={phone.comment ? phone.comment : ''}
                                    />
                                </div>
                            </FormGroup>
                        </FormControl>
                    </form>
                    <div>
                        <Button
                            className={classes.button}
                            variant={'contained'}
                            onClick={onSubmit}
                            color={'primary'}
                        > </Button>
                    </div>
                </Grid>
            </Grid>
        </div>

    );
};

