import React, {ChangeEvent, FC, useState} from 'react';
import 'date-fns';
import {
    Button,
    FormControl,
    FormGroup,
    Grid,
    IconButton,
    InputLabel,
    MenuItem,
    Select,
    TextField
} from "@material-ui/core";
import {useActions} from "../../../store/hooks/useActions";
import {TargetType} from "../searchPage/SearchPage";
import {useStylesAddPage} from "./syles/addPage.styles";
import {useHistory} from 'react-router-dom';
import {PATH} from "../../../routes/Routes";
import Menu from "../../../shared/components/Menu";
import Avatar from "../editPage/avatar/Avatar";
import PhoneForm from "../editPage/phone/PhoneForm";
import AttachmentsForm from "../editPage/attachments/AttachmentsForm";
import {ContactInterface} from "../../contactList/types/contact.interface";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import {KeyboardDatePicker} from "@material-ui/pickers";
import {formatDate} from "../../../utils/utils";

const AddPage: FC = () => {
    const classes = useStylesAddPage()
    const history = useHistory()

    const [errors, setError] = useState({
        name: '',
        surname: '',
        patronymic: '',
    })

    const {addContact, logOut} = useActions()

    const [contactInfo, setContactInfo] = useState({} as ContactInterface)
    const [contactAddress, setContactAddress] = useState({})
    const [gender, setGender] = useState('')
    const [logo, setLogo] = useState({})
    const [newPhones, setNewPhones] = useState([])
    const [newAttachments, setNewAttachments] = useState([])

    const [selectedDate, setSelectedDate] = React.useState<Date | null>(
        new Date(''),
    );

    const handleDateChange = (date: Date | null) => {
        setSelectedDate(date);
    };

    const changeContactInfoHandler = (event: ChangeEvent<HTMLInputElement>) => {

        const target: TargetType = (event.target)
        const isDate = target.name === 'birthDate'
        const replaceStr = event.target.value.replace(/-/g, ' ').split(' ').reverse().join('.')
        setContactInfo({
            ...contactInfo,
            [target.name]: isDate ? replaceStr : target.value,
        })
    }

    const changeContactAddressHandler = (event: ChangeEvent<HTMLInputElement>) => {

        const target: TargetType = (event.target)

        setContactAddress({
            ...contactAddress,
            [target.name]: target.value,
        })
    }

    const ChangeGender = (event: any) => {
        setGender(event.target.value as string);
    };

    const exitClickHandler = () => {
        logOut()
    }
    const birthDate = formatDate(selectedDate, 'DD.MM.yyyy')

    const onSubmit = () => {
        const contact = {
            contact: {
                ...contactInfo,
                address: contactAddress,
                birthDate: birthDate,
                gender: gender,
                logo: logo,
                phones: newPhones,
                attachments: newAttachments
            }
        }
        addContact(contact)
        history.push(PATH.HOME)
    }

    const validation = (event: React.FocusEvent<HTMLInputElement>) => {
        const target = event.target
        const regexRange = /^[--,0-9]{3,10}$/;
        if (target.name === 'name') {
            if (!regexRange.test(target.value)) {
                setError({...errors, name: '  '})
            } else if (regexRange.test(target.value)) {
                setError({...errors, name: ''})
            }
        }
        if (target.name === 'surname') {
            if (!regexRange.test(target.value)) {
                setError({...errors, surname: '  '})
            } else if (regexRange.test(target.value)) {
                setError({...errors, surname: ''})
            }
        }
        if (target.name === 'patronymic') {
            if (!regexRange.test(target.value)) {
                setError({...errors, patronymic: '  '})
            } else if (regexRange.test(target.value)) {
                setError({...errors, patronymic: ''})
            }
        }
        return errors
    }

    return (
        <div className={classes.addPageBG}>
            <Menu auth={''} exitClickHandler={exitClickHandler}/>
            <div className={classes.container}>
                <IconButton className={classes.prevButton}
                            onClick={() => history.push(PATH.HOME)}
                            aria-label="close">
                    <ArrowBackIcon/>
                </IconButton>
                <div className={classes.avatar}><Avatar setLogo={setLogo} contact={contactInfo}/></div>
                <div className={classes.addPageWrapper}>
                    <h2 className={classes.title}>  </h2>
                    <Grid container justifyContent="center">
                        <Grid item xs={10}>
                            <form onSubmit={onSubmit}>
                                <FormControl className={classes.form}>
                                    <FormGroup className={classes.row}>
                                        <div>
                                            <div className={classes.period}>
                                                <div>
                                                    <TextField className={classes.input}
                                                               title={'    '}
                                                               label=""
                                                               name={"name"}
                                                               type="search"
                                                               onBlur={validation}
                                                               onChange={changeContactInfoHandler}
                                                    />
                                                    {errors.name ?
                                                        <div className={classes.error}>{errors.name}</div> : null}
                                                </div>
                                                <div>
                                                    <TextField className={classes.input}
                                                               title={'    '}
                                                               label=""
                                                               name={"surname"}
                                                               type="search"
                                                               onBlur={validation}
                                                               onChange={changeContactInfoHandler}
                                                    />
                                                    {errors.surname ?
                                                        <div className={classes.error}>{errors.surname}</div> : null}
                                                </div>
                                                <div>
                                                    <TextField className={classes.input}
                                                               title={'    '}
                                                               label=""
                                                               name={"patronymic"}
                                                               type="search"
                                                               onBlur={validation}
                                                               onChange={changeContactInfoHandler}
                                                    />
                                                    {errors.patronymic ?
                                                        <div className={classes.error}>{errors.patronymic}</div> : null}
                                                </div>
                                                <FormControl className={classes.gender}>
                                                    <InputLabel id="demo-simple-select-label"></InputLabel>
                                                    <Select
                                                        labelId="demo-simple-select-label"
                                                        id="demo-simple-select"
                                                        value={gender}
                                                        onChange={ChangeGender}
                                                    >
                                                        <MenuItem value={''}/>
                                                        <MenuItem value={''}></MenuItem>
                                                        <MenuItem value={''}></MenuItem>
                                                    </Select>
                                                </FormControl>


                                                <TextField className={classes.input}
                                                           label=" "
                                                           name={"maritalStatus"}
                                                           type="search"
                                                           onChange={changeContactInfoHandler}
                                                />
                                                <TextField className={classes.input}
                                                           label=""
                                                           name={"nationality"}
                                                           type="search"
                                                           onChange={changeContactInfoHandler}
                                                />
                                            </div>
                                            <div className={classes.dateWrapper}>
                                                <div>
                                                    <h3 className={classes.title}></h3>
                                                </div>
                                                <div className={classes.period}>
                                                            <KeyboardDatePicker
                                                                margin="normal"
                                                                id="date-picker-dialog"
                                                                format="dd.MM.yyyy"
                                                                helperText=" "
                                                                value={selectedDate}
                                                                onChange={handleDateChange}
                                                                KeyboardButtonProps={{
                                                                    'aria-label': 'change date',
                                                                }}
                                                                error={false}
                                                            />
                                                </div>
                                            </div>
                                            <div>
                                                <h3 className={classes.title}></h3>
                                                <div className={classes.addressWrapper}>
                                                    <br/><TextField className={classes.input}
                                                                    label=""
                                                                    name={"country"}
                                                                    type="search"
                                                                    onChange={changeContactAddressHandler}
                                                />
                                                    <TextField className={classes.input}
                                                               label=""
                                                               name={"city"}
                                                               type="search"
                                                               onChange={changeContactAddressHandler}
                                                    />
                                                    <TextField className={classes.input}
                                                               label=""
                                                               name={"street"}
                                                               type="search"
                                                               onChange={changeContactAddressHandler}
                                                    />
                                                    <TextField className={classes.input}
                                                               label=" "
                                                               name={"building"}
                                                               type="number"
                                                               onChange={changeContactAddressHandler}
                                                    />
                                                    <TextField className={classes.input}
                                                               label=" "
                                                               name={"flat"}
                                                               type="number"
                                                               onChange={changeContactAddressHandler}
                                                    />
                                                    <TextField className={classes.input}
                                                               label=""
                                                               name={"zipCode"}
                                                               type="number"
                                                               onChange={changeContactAddressHandler}
                                                    />
                                                </div>
                                            </div>

                                        </div>

                                        <PhoneForm setNewPhones={setNewPhones} newPhones={newPhones} contact={contactInfo}/>
                                        <AttachmentsForm contact={contactInfo} newAttachments={newAttachments} setNewAttachments={setNewAttachments}/>

                                        <div className={classes.buttonWrapper}>
                                            <Button
                                                className={classes.editButton}
                                                type={'submit'}
                                                variant={'contained'}
                                                color={'primary'}
                                            > </Button>

                                        </div>
                                    </FormGroup>
                                </FormControl>
                            </form>
                        </Grid>
                    </Grid>
                </div>
            </div>
        </div>
    );
};

export default AddPage;
