import React, {SyntheticEvent, useEffect, useRef, useState} from 'react';
import {useActions} from "../../store/hooks/useActions";
import {useTypeSelector} from "../../store/hooks/useTypeSelector";
import {Button, CircularProgress, Grid, IconButton, Typography} from "@material-ui/core";

import EditIcon from "@material-ui/icons/Edit";
import SearchIcon from '@material-ui/icons/Search';
import {ContactInterface} from "./types/contact.interface";
import {NavLink, useHistory} from 'react-router-dom';
import {Delete} from "@material-ui/icons";
import DeleteModal from "../pages/deleteModal/DeleteModal";
import SearchPage from "../pages/searchPage/SearchPage";
import {PATH} from "../../routes/Routes";
import {useStylesContactList} from "./styles/contactListStyles";
import Menu from "../../shared/components/Menu";
import {DataGrid, GridCellParams, GridColDef, GridRowId, ruRU} from "@material-ui/data-grid";

const ContactList = () => {

    const columns: GridColDef[] = [
        {field: 'name', headerName: '', width: 160, filterable: false, sortable: false, hide: true},
        {field: 'surname', headerName: '', width: 160, filterable: false, sortable: false, hide: true},
        {field: 'patronymic', headerName: '', width: 160, filterable: false, sortable: false, hide: true},
        {
            field: '',
            headerName: '',
            width: 250,
            filterable: false,
            headerClassName: 'column',
            headerAlign: 'center',
            sortable: false,
            renderCell: (params: GridCellParams) => {
                return <span>{`${params.row.name} ${params.row.surname} ${params.row.patronymic}`}</span>
            },
        },
        {
            field: 'birthDate',
            headerName: ' ',
            width: 170,
            filterable: false,
            headerClassName: 'column',
            sortable: false,
            headerAlign: 'center',

        },
        {
            field: 'gender',
            headerName: '',
            width: 80,
            filterable: false,
            sortable: false,
            hide: true,
            headerAlign: 'center'
        },
        {
            field: 'maritalStatus',
            headerName: ' ',
            width: 200,
            filterable: false,
            sortable: false,
            hide: true
        },
        {field: 'nationality', headerName: '', width: 140, filterable: false, sortable: false, hide: true},
        {
            field: 'address',
            headerName: '',
            headerAlign: 'center',
            headerClassName: 'column',
            flex: 1,
            width: 350,
            filterable: false,
            sortable: false,
            renderCell: (params: GridCellParams) => {
                return <span>{params.row.address.fullAddress}</span>
            }
        },
        {field: 'email', headerName: 'Email', width: 140, filterable: false, sortable: false, hide: true},
        {
            field: 'currentJob',
            headerName: ' ',
            width: 160,
            filterable: false,
            headerClassName: 'column',
            sortable: false,
            headerAlign: 'center',
        },
        {
            field: '',
            headerName: '',
            width: 155,
            filterable: false,
            sortable: false,
            editable: true,
            headerClassName: 'column',
            headerAlign: 'center',
            renderCell: (el) => {
                return <IconButton
                    aria-label="edit"
                    id={String(el.id)}
                    onClick={contactClickHandler}
                >
                    <NavLink to={'/contacts/edit'}>
                        <EditIcon/>
                    </NavLink>
                </IconButton>
            }
        },
        {
            field: '',
            headerName: '',
            width: 110,
            filterable: false,
            sortable: false,
            headerAlign: 'center',
            headerClassName: 'column',
            renderCell: (el) =>
                <IconButton
                    aria-label="del"
                    id={String(el.id)}
                    onClick={() => handleOpenModal([String(el.id)])}
                >
                    <Delete/>
                </IconButton>
        },
    ];

    const usePrevious = (value: any) => {
        const ref = useRef();
        useEffect(() => {
            ref.current = value;
        });
        return ref.current;
    }
    const classes = useStylesContactList();
    const [items, setItems] = useState<ContactInterface[]>([])
    const [selectionModel, setSelectionModel] = useState<GridRowId[]>([]);
    const [open, setOpen] = React.useState(false);
    const [openSearch, setOpenSearch] = React.useState(false);
    const {getContacts, setPage, setTake, logOut} = useActions()
    const {isLoading, data, maxUsers, page, take} = useTypeSelector(state => state.contacts)
    const {isDeleteLoading} = useTypeSelector(state => state.delete)

    const prevVal = usePrevious(data)
    const history = useHistory()
    sessionStorage.setItem('contactsId', JSON.stringify(selectionModel));
    sessionStorage.setItem('contacts', JSON.stringify(items));

    const handleOpenModal = (id: GridRowId[]) => {
        setSelectionModel(id)
        setOpen(true);
    };
    const handleCloseModal = () => {
        setOpen(false);
    };

    const updateFullAddress = (data: ContactInterface[]) => {
        const updatedData = [...data]
        updatedData.map((item: ContactInterface) => {
            return item.address.fullAddress = `${item.address.zipCode} ${item.address.country}, . ${item.address.city}, . ${item.address.street} ${item.address.building}/${item.address.flat}`
        })
        setItems(updatedData)
    }

    const handlePaginationChange = (page: number) => {
        setPage(page + 1)
        setTake(take)
        getContacts()
    };
    const checkedCurrenContacts = (params: GridRowId[]) => {
        setSelectionModel(params)
    }

    const contactClickHandler = (event: SyntheticEvent) => {
        const targetID = event.currentTarget.id
        const contactsForUpdate = [...data]
        const currentContact: ContactInterface = contactsForUpdate.find(target => target.id === targetID);
        history.push(`${PATH.EDIT}/${currentContact.id}`, {contact: currentContact})
    }

    const searchClickHandler = (event: SyntheticEvent) => {
        if (event.currentTarget) {
            setOpenSearch(true)
        }
    }

    const searchClickHandlerClose = () => {
        setOpenSearch(false)
    }

    const deleteContact = (event: SyntheticEvent) => {
        const id = event.currentTarget.id
        const checkedContacts: Array<string> = []
        setSelectionModel([...checkedContacts, id])
        // deleteContacts([...checkedContacts, id])
        handleCloseModal()
    }

    const exitClickHandler = () => {
        logOut()
    }

    useEffect(() => {
        getContacts()
        if (data && data.length > 0) {
            updateFullAddress(data)
        }
    }, [])

    useEffect(() => {
        if (data && data.length > 0) {
            if (data !== prevVal) {
                updateFullAddress(data)
            }
        } else {
            setItems([])
        }
    }, [data])


    if (isLoading || !data) {
        return <CircularProgress
            className={classes.preloader}
            size={60}
            color="secondary"
        />
    }

    return (

        <div className={classes.root}>
            <Menu auth={''} exitClickHandler={exitClickHandler}/>
            <div className={classes.container}>
                <Grid
                    className={classes.headerWrapper}
                    container
                    direction="row"
                    justifyContent="space-between"
                    alignItems="center"
                > <NavLink className={classes.link} to={'/contacts/create'}>
                    <Button
                        variant="outlined"
                        color="primary"
                    >
                          
                    </Button>
                </NavLink>
                    <div>
                        <Button
                            onClick={() => handleOpenModal(selectionModel)}
                            disabled={selectionModel.length === 0}
                            className={classes.deleteButton}
                            variant="outlined"
                            color="secondary"
                        >
                             
                            <Delete/>
                        </Button>
                            <NavLink className={classes.link} to={'/contacts/email'}>
                                <Button
                                    disabled={selectionModel.length === 0}
                                    variant="contained"
                                    size="large"
                                    color="primary"
                                    target="_top"
                                    rel="noopener noreferrer"
                                    href={``}
                                >
                                    <Typography variant="button" className={classes.emailButtonText}>
                                         E-mail
                                    </Typography>
                                </Button>
                            </NavLink>
                        <Button
                            onClick={searchClickHandler}
                            className={classes.searchButton}
                            variant="outlined"
                            color="primary"
                        >
                            <SearchIcon/>
                        </Button>

                    </div>
                </Grid>

                {openSearch ? <SearchPage searchClickHandlerClose={searchClickHandlerClose}/> : null}

                <DataGrid
                    className={classes.contactsForm}
                    rows={items}
                    columns={columns}
                    pageSize={take}
                    page={page - 1 || 0}
                    rowCount={maxUsers}
                    autoHeight
                    loading={isDeleteLoading}
                    paginationMode={'server'}
                    rowsPerPageOptions={[5, 10, 25, ] }
                    onPageChange={handlePaginationChange}
                    onPageSizeChange={handlePaginationChange}
                    sortingMode={'server'}
                    disableSelectionOnClick
                    checkboxSelection
                    localeText={"props" in ruRU ? ruRU.props.MuiDataGrid.localeText : undefined}
                    onSelectionModelChange={checkedCurrenContacts}
                    selectionModel={selectionModel}
                />

                <DeleteModal open={open}
                             onClose={handleCloseModal}
                             selectionModel={selectionModel}
                             deleteContact={deleteContact}
                />
            </div>
        </div>
    );
}

export default ContactList
