import React, {ChangeEvent, FormEvent, useEffect, useState} from 'react';
import {Button, FormControl, FormGroup, Grid, TextField} from "@material-ui/core";
import {useActions} from "../../../store/hooks/useActions";
import {useStylesSearchPage} from "./styles/styles";
import {SearchParamsInterface} from "./types/searcParams.interface";
import {KeyboardDatePicker} from "@material-ui/pickers";
import {formatDate} from "../../../utils/utils";
import {MaterialUiPickersDate} from "@material-ui/pickers/typings/date";

export type TargetType = {
    name: string
    value: string
}

type SearchPanelType = {
    searchClickHandlerClose: () => void
}

const SearchPanel = (props: SearchPanelType) => {
    const classes = useStylesSearchPage()
    const {getContacts, setSearchParams, setPage} = useActions()
    const savedSearch = JSON.parse(sessionStorage.getItem('search') || '{}');
    const [search, setSearch] = useState(savedSearch || {} as SearchParamsInterface)
    const [dateFrom, setDateFrom] = useState<MaterialUiPickersDate>(null);
    const [dateTo, setDateTo] = useState<MaterialUiPickersDate>(null);

    const changeContactInfoHandler = (event: ChangeEvent<HTMLInputElement>) => {
        const target: TargetType = (event.target)
        const isDate = target.name === 'dateFrom' || target.name === 'dateTo'
        let replaceStr
        if (isDate && target.value.length === 10) {
            replaceStr = formatDate(target.value, 'DD.MM.yyyy')
        }

        setSearch({
            ...search,
            [target.name]: isDate ? replaceStr : target.value,
        })
    }

    const handleDateFromChange = (date: Date | null) => {
        setDateFrom(date);
        const newDate = formatDate(date, 'DD.MM.yyyy')
        setSearch({...search, dateFrom: newDate})
    };
    const handleDateToChange = (date: Date | null) => {
        setDateTo(date);
        const newDate = formatDate(date, 'DD.MM.yyyy')
        setSearch({...search, dateTo: newDate})
    };

    const onSubmit = (event: FormEvent) => {
        event.preventDefault()
        sessionStorage.setItem('search', JSON.stringify(search));
        setPage(1)
        setSearchParams(search)
        getContacts()
    }

    const discharge = () => {
        sessionStorage.clear()
        setSearch({})
    }

    useEffect(() => {
        if (search.dateFrom) {
            setDateFrom(new Date(search.dateFrom))
        }
        if (search.dateTo) {
            setDateTo(new Date(search.dateTo))
        }
    }, [search])

    return (
        <div className={classes.searchPanel}>
            <Grid container justifyContent="center">
                <Grid item xs={10} className={classes.searchWrapper}>
                    <form onSubmit={onSubmit}>
                        <FormControl className={classes.form}>
                            <FormGroup className={classes.row}>
                                <h2 className={classes.title}> </h2>
                                <div className={classes.searchTable}>
                                    <div className={classes.period}>
                                        <TextField className={classes.input}
                                                   label=""
                                                   name={"name"}
                                                   type="search"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.name}
                                        />
                                        <TextField className={classes.input}
                                                   label=""
                                                   name={"surname"}
                                                   type="search"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.surname}
                                        />
                                        <TextField className={classes.input}
                                                   label=""
                                                   name={"patronymic"}
                                                   type="search"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.patronymic}
                                        />

                                        <TextField className={classes.input}
                                                   label=""
                                                   name={"gender"}
                                                   type="search"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.gender}
                                        />
                                        <TextField className={classes.input}
                                                   label=" "
                                                   name={"maritalStatus"}
                                                   type="search"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.maritalStatus}
                                        />
                                        <TextField className={classes.input}
                                                   label=""
                                                   name={"nationality"}
                                                   type="search"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.nationality}
                                        />
                                        <TextField className={classes.input}
                                                   label="Email"
                                                   name={"email"}
                                                   type="search"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.email}
                                        />
                                        <TextField className={classes.input}
                                                   label=" "
                                                   name={"currentJob"}
                                                   type="search"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.currentJob}
                                        />
                                    </div>
                                    <div className={classes.dateWrapper}>
                                        <div>
                                            <h3 className={classes.title}></h3>
                                        </div>
                                        <div className={classes.period}>
                                            <KeyboardDatePicker
                                                name={"dateFrom"}
                                                helperText=""
                                                className={classes.date}
                                                margin="normal"
                                                id="date-picker-dialog"
                                                format="dd.MM.yyyy"
                                                value={formatDate(dateFrom, 'DD.MM.yyyy')}
                                                onChange={handleDateFromChange}
                                                onInput={changeContactInfoHandler}
                                                KeyboardButtonProps={{
                                                    'aria-label': 'change date',
                                                }}
                                                error={false}
                                            />
                                            <KeyboardDatePicker
                                                name={"dateTo"}
                                                helperText=""
                                                className={classes.date}
                                                margin="normal"
                                                id="date-picker-dialog"
                                                format="dd.MM.yyyy"
                                                value={formatDate(dateTo, 'DD.MM.yyyy')}
                                                onChange={handleDateToChange}
                                                onInput={changeContactInfoHandler}
                                                KeyboardButtonProps={{
                                                    'aria-label': 'change date',
                                                }}
                                                error={false}
                                            />
                                        </div>
                                    </div>
                                    <h3 className={classes.title}></h3>
                                    <div className={classes.addressWrapper}>
                                        <TextField className={classes.input}
                                                   label=""
                                                   name={"country"}
                                                   type="search"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.country}
                                        />
                                        <TextField className={classes.input}
                                                   label=""
                                                   name={"city"}
                                                   type="search"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.city}
                                        />
                                        <TextField className={classes.input}
                                                   label=""
                                                   name={"street"}
                                                   type="search"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.street}

                                        />
                                        <TextField className={classes.input}
                                                   label=" "
                                                   name={"building"}
                                                   type="number"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.building}

                                        />
                                        <TextField className={classes.input}
                                                   label=" "
                                                   name={"flat"}
                                                   type="number"
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.flat}
                                        />
                                        <TextField className={classes.input}
                                                   label=""
                                                   name={"zipCode"}
                                                   type="search"
                                                   placeholder={'6-  '}
                                                   inputProps={{pattern: "[0-9]{6}"}}
                                                   onChange={changeContactInfoHandler}
                                                   defaultValue={savedSearch.zipCode}
                                        />
                                    </div>

                                </div>
                                <div className={classes.buttonWrapper}>

                                    <Button type={'submit'} variant={'contained'} color={'primary'}
                                            onClick={discharge}></Button>
                                    <Button type={'submit'} variant={'contained'} color={'primary'}
                                    ></Button>
                                    <Button type={'submit'} variant={'contained'} color={'primary'}
                                            onClick={props.searchClickHandlerClose}></Button>
                                </div>
                            </FormGroup>
                        </FormControl>
                    </form>
                </Grid>
            </Grid>
        </div>
    );
};

export default SearchPanel;
